
.eventCard {
    background: rgb(200, 200, 200);
    color: black;
    padding: 5px 0;
    border-radius: 10px;
    margin: 5px 0;
  }
  
  .eventCard p {
    background: white;
    border-radius: 10px;
    padding: 5px;
    margin: 5px 5px;
  }
  
  .detailsWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .detailsWrapper details summary {
    flex-grow: 1;
    font-size: 12px;
  }
  
  .category {
    font-size: 12px;
  }
  
  .description {
    font-size: 12px;
  }
  
  .email {
    font-size: 12px;
  }